export default {
	name: 'xinput',
	data() {
		return {
			inputValue: ''
		}
	},
	props: {
		title: String,
		placeHolderText: String,
		xtype: {
			type: String,
			default: 'text'
		},
		xpassword: {
			type: Boolean,
			default: false
		}
	},
	methods: {
		bindInput(e) {
			this.inputValue = e.detail.value
			this.$emit('inputValue', {
				inputValue: this.inputValue
			})
		}
	}
}
